/* @import './styles/bootstrap.css'; */

html {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    width: 100%;
    font-size: 14px;
    font-family: 'Oxygen', sans-serif;
    /* font-size: 100%; */
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#root {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

input {
    font-size: 100%;
}

button {
    font-size: 100%;
}

/* Common styles. */

.field-label {
    /* background-color: rgba(200, 200, 200, 0.5); */
    padding: 3px 3px 3px 0;
    font-weight: bold;
    color: rgba(150, 150, 150, 1);
}

/* antd overrides */

.ant-btn>.anticon+span,
.ant-btn>span+.anticon {
    margin-left: 2px
}
.App {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.App-header {
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
}

.App-body {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    margin: 0;
}
.Organizations {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
}

.Organizations>.row {
    flex: 0 0 auto;
    /* border: 1px silver dashed; */
    /* border-bottom: 1px rgba(200, 200, 200, 0.8) solid; */
    padding: 4px;
    margin: 4px 0 20px;
    display: flex;
    flex-direction: row;
}

.Organizations>.row>.col1 {
    flex: 1 1;
    margin-left: 10px;
}

.Organizations>.row>.col2 {
    flex: 0 0 64px;
}

.Organizations .orgName {
    font-weight: bold;
    font-size: 110%;
    cursor: pointer;
}

.Organizations .Organizations-info-table {
    width: 100%
}

.Organizations .Organizations-info-table th {
    text-align: right;
    font-weight: bold;
    color: rgba(150, 150, 150, 1);
    width: 8em;
    vertical-align: top;
}

.Organizations .Organizations-info-table td {
    /* width: 50% */
    vertical-align: top;
}
.OrganizationsBrowser {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.OrganizationsBrowser>.searchBarRow {
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
    padding: 6px 0;
    border-bottom: 1px rgba(200, 200, 200, 0.5) solid;
}

.OrganizationsBrowser>.bodyRow {
    flex: 1 1;
    display: flex;
    flex-direction: row;
}

.OrganizationsBrowser>*>.col1 {
    flex: 0 0 14em;
    margin: 0 0 0 10px;
    display: flex;
    flex-direction: column;
}

.OrganizationsBrowser>*>.col2 {
    flex: 1 1;
    margin: 0 0 0 0;
    display: flex;
    flex-direction: column;
}

.OrganizationsBrowser>*>.sortCol {
    flex: 1.5 1;
    margin: 0 0 0 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.OrganizationsBrowser>*>.searchBarCol {
    flex: 1 1;
    margin: 0 10px 0 0;
    display: flex;
    flex-direction: column;
}

.OrganizationsBrowser-controlArea {}

.OrganizationsBrowser-controlArea>.controlGroup {
    border: 1px rgba(200, 200, 200, 0.5) solid;
    margin-bottom: 14px;
    padding: 4px;
    border-radius: 8px;
}

.OrganizationsBrowser-controlArea>.controlGroup .radio {
    display: block;
    height: 1.8em;
}

.OrganizationsBrowser-controlArea>.controlGroup>.header {
    font-weight: bold;
    color: rgba(150, 150, 150, 1);
    text-align: center;
}

.OrganizationsBrowser-controlArea>.controlGroup>.row {
    padding: 4px;
    cursor: pointer;
    /* height: 2em; */
}

.OrganizationsBrowser-controlArea>.controlGroup>.row>.col1 {
    display: inline-block;
    width: 10%;
}

.OrganizationsBrowser-controlArea>.controlGroup>.row>.col2 {
    /* display: inline-block; */
    /* width: 90% */
}

.OrganizationsBrowser-searchBar {
    flex: 0 0 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.OrganizationsBrowser-searchBar>.col1 {
    flex: 2 1;
}

.OrganizationsBrowser-searchBar>.col2 {
    flex: 1 1;
    text-align: right;
}

.OrganizationsBrowser-searchBar input {
    width: 20em;
    height: 100%;
}

.OrganizationsBrowser-searchBar button {
    height: 100%;
    padding: 4px;
}

.OrganizationsBrowser-searchBar .message {
    display: inline-block;
    margin-left: 1em;
}
.Header {
    flex: 0 0 40px;
    font-weight: bold;
    font-size: 100%;
    text-align: left;
    /* padding: 4px; */
    /* margin-bottom: 10px; */
    padding-left: 10px;
    background-color: rgba(200, 200, 200, 0.5);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
.NewOrganization {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.NewOrganization h3 {
    padding: 8px;
    /* background-color: rgba(200, 200, 200, 0.5); */
    border-bottom: 2px rgba(200, 200, 200, 0.8) solid;
    text-align: center;
}

.NewOrganization .mainRow {
    flex: 1 1;
    display: flex;
    flex-direction: row;
    overflow-y: auto;
}

.NewOrganization .footerRow {
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
    border-top: 1px silver solid;
}

.NewOrganization .footerRow .col2 {
    align-items: center;
}

.NewOrganization .editorColumn {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    margin-right: 10px;
}

.NewOrganization .previewColumn {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.NewOrganization .editor,
.NewOrganization .preview {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.NewOrganization .row {
    flex: 0 0 auto;
    min-height: 30px;
    display: flex;
    flex-direction: row;
    /* height: 30px; */
    margin: 4px 0;
}

.NewOrganization .col1 {
    flex: 0 0 7em;
    font-weight: bold;
    text-align: left;
    /* color: rgba(100, 100, 100, 1); */
    /* border: 1px silver solid; */
}

.NewOrganization .col2 {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    /* border: 1px silver solid; */
}

.NewOrganization .editor input {
    box-sizing: border-box;
    display: inline-block;
    width: 100%;
}

.NewOrganization .editor textarea {
    flex: 1 1;
    box-sizing: border-box;
    display: inline-block;
    width: 100%;
}

.NewOrganization .editor .footer {
    flex: 0 0 30px;
    text-align: center;
    padding: 6px;
}

.NewOrganization .editor .gravatarHash {
    height: 64px;
}

.NewOrganization .preview .id {
    font-family: monospace;
}

.NewOrganization .preview .description {
    padding: 8px;
    border: 1px rgba(200, 200, 200, 0.5) solid;
    /* border-radius: 8px; */
    flex: 1 1;
    overflow-y: auto;
    box-shadow: 4px 4px 4px rgba(200, 200, 200, 0.5);
}

.NewOrganization .preview .gravatarHash {
    height: 64px;
}
.ViewOrganization {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.ViewOrganization .mainRow {
    flex: 1 1;
    display: flex;
    flex-direction: row;
    margin-top: 10px;
}

.ViewOrganization .mainRow>.mainColumn {
    flex: 2 1;
    display: flex;
    flex-direction: column;
    margin-right: 10px;
}

.ViewOrganization .mainRow>.infoColumn {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.ViewOrganization form.table {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.ViewOrganization .table>.row {
    flex: 0 0 auto;
    min-height: 30px;
    display: flex;
    flex-direction: row;
    /* height: 30px; */
    margin-bottom: 8px;
}

.ViewOrganization .table.infoTable>.row {
    flex: 0 0 auto;
    min-height: 20px;
    display: flex;
    flex-direction: row;
    /* height: 30px; */
    margin-bottom: 8px;
}

.ViewOrganization .table.infoTable>.row>.col0 {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.ViewOrganization .table.infoTable>.row>.col0>.ownerTable {
    flex: 1 1;
    display: flex;
    flex-direction: row;
}

.ViewOrganization .table.infoTable>.row>.col0>.ownerTable>.avatarCol {
    flex: 0 0 60px;
}

.ViewOrganization .table.infoTable>.row>.col0>.ownerTable>.proprietorCol {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    padding-left: 4px;
}

.ViewOrganization .ownerTable .owner {
    font-weight: bold;
}

.ViewOrganization .table.infoTable>.row>.col1 {
    flex: 0 0 8em;
    font-weight: bold;
    text-align: left;
    color: rgba(100, 100, 100, 1);
}

.ViewOrganization .table.infoTable>.row>.col2 {}

.ViewOrganization .table>.row>.col1 {
    flex: 0 0 12em;
    font-weight: bold;
    text-align: left;
    color: rgba(100, 100, 100, 1);
    /* border: 1px silver solid; */
}

.ViewOrganization .table>.row>.col2 {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    /* border: 1px silver solid; */
}

