}

.ViewOrganization .org .nameAndLogo {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
}

.ViewOrganization .org .nameAndLogo .avatar {
    flex: 0 0 64px;
    height: 64px;
}

.ViewOrganization .org .nameAndLogo .nameAndLink {
