.Auth {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.Auth .Auth-authorized {}

.Auth .Auth-unauthorized {
    color: red;
    padding: 4px;
    border: 1px red solid;
}

.Auth .Auth-error {
    color: red;
    padding: 4px;
    border: 1px red solid;
}

.Auth .Auth-form {
    flex: 0 0 auto;
    border: 1px blue dotted;
    padding: 8px;
}

.Auth .Auth-form input {
    width: 20em
}

.Auth .Auth-toolbar {
    flex: 0 0 auto;
    border: 1px blue dotted;
    padding: 8px;
}
